.header-list {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.header-list > * + * {
  margin-left: 2rem;
}

/* @supports (gap: 10px) and (display: flex) {
  div {
    display: flex;
    gap: 10px;
  }
} */

.link:link,
.link:visited {
  font-size: 1.6rem;
  display: inline-block;
  color: black;
  transition: all 0.3s;
}

.link:hover,
.link:active {
  color: #fa8e00;
  transform: scale(1.02);
}

@media (max-width: 39em) {
  .header-list > * + * {
    margin-left: 1rem;
  }
}

/* 432px */
@media (max-width: 27em) {
  .header-list > * + * {
    margin-left: 0.3rem;
  }

  .link:link,
  .link:visited {
    font-size: 0.8rem;
  }
}
